html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

h1, h2, h3, h4, h5 {
  color: #00252e;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

a, a:visited, a:active {
  text-decoration: none;
}

button {
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  border: none;
  border-radius: 2rem;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5rem;
}
.header__logo {
  width: 30%;
}
.header__logo img {
  width: 100%;
}
.header button {
  background-color: white;
  padding: 0.25rem 1.4375rem 0.375rem 1.375rem;
  border-radius: 2rem;
  font-size: 0.625rem;
  border: none;
  font-weight: bold;
}
.header button:hover {
  color: #808d99;
}

.header_hero_container {
  padding: 1.6875rem 1rem 2.3125rem 1rem;
  background-image: url(/images/bg-hero-mobile.svg);
  background-color: #ebfbff;
  height: 45.0625rem;
}

.hero {
  text-align: center;
  height: 100%;
  padding: 0 1.1875rem 0 1.1875rem;
}
.hero h1 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
.hero p {
  font-size: 1rem;
  color: #00252e;
  line-height: 1.5rem;
  margin-bottom: 2rem;
}
.hero button {
  background: #ff52bf;
  color: white;
  width: 15rem;
  height: 2.5rem;
  margin-bottom: 3.5rem;
}
.hero button:hover {
  background: #ff99db;
}
.hero img {
  width: 100%;
}